.shopcategory-banner{
    display: block;
    margin : 30px auto;
    width :82%;
}
.shopcategory-indexsort{
    display: flex;
    margin : 0px 170px;
    justify-content: space-between;
    align-items: center; 
}
.shopcategory-sort{
    padding: 10px 20px ;
    border-radius: 40px;
    border:1px solid #888;
    width: 70px;
}
.shopcategory-indexsort p span {
font-weight: 600;
}
.shopcategory-product{
    margin: 20px 170px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
   row-gap: 60px;
   
}
.shopcategory-loadmore{
    display: flex;
    justify-content: center;
    align-items: center;
    margin:150px auto ;
    width: 233px;
    height: 70px;
    border-radius: 75px;
    color: #787878;
    background-color: #ededed;
    font-size: 18px;
    font-weight: 500;
    cursor: pointer;
}
@media(max-width: 1280px){
    .shopcategory-banner{
        margin: 20px auto;
        width: 90%;
    }
    .shopcategory-indexsort{
        width: 90%;
        margin: auto;
    }
    .shopcategory-sort{
        padding: 5px 10px;

    }
    .shopcategory-indexsort p {
        font-size: 12px;
    }
    .shopcategory-product{
        width: 90%;
        margin: 10px auto;
        row-gap: 40px;
    }
    .shopcategory-product .item{
        margin : auto;
    }
    .shopcategory-loadmore{
        margin: 100px auto;
        width: 200px;
        height: 60px;
        font-size: 16px;
    }
}
@media(max-width: 800px){
    .shopcategory-banner{
        width: 95%;
    }
    .shopcategory-product{
        width: 95%;
    }
    .shopcategory-loadmore{
        margin: 60px auto;
        width: 150px;
        height: 40px;
        font-size: 13px;
    }
}
@media(max-width: 500px){
    .shopcategory-product{
        display: grid;
        grid-template-columns: 1fr 1fr;
    }
    .shopcategory-product .item img{
        width: 150px;
    }
}