.loginsignup{
    height: 100vh;
    width  :100%;
    background-color: rgba(209, 18, 209, 0.425);
    padding-top: 100px;
}
.loginsignup-container{
    width: 580px;
    height: 600px;
    background-color: white;
    margin: auto;
    padding: 40px 60px;
}
.loginsignup-container h1{
    margin: 20px 0px;

}
.loginsignup-fields{
    display: flex;
    flex-direction: column;
    gap: 30px;
    margin-top: 30px;
}
.loginsignup-fields input{
    height: 70px;
    width: 100%;
    padding-left: 20px;
    border: 1px solid #9c9c9c;
    outline: none;
    color:#5c5c5c;
    font: 20px;
    border-radius: 50px;
}
.loginsignup-container button {
    width: 580px;
    height: 70px;
    color:white;
    background-color: #ff4141;
    margin-top: 30px;
    border: none;
    font-size: 25px;
    font-weight: 500;
    cursor: pointer;
    border-radius: 75px;
}
.loginsignup-login{
    margin-top: 20px;
    color: #5c5c5c;
    font-size: 18px;
    font-weight: 500;

}
.loginsignup-login span {
   color: #ff4141;
   font-weight: 600; 
}
.login-signup-agree{
    display: flex;
    align-items: center;
    margin-top: 25px;
    gap: 20px;
    color: #5c5c5c;
    font-size: 18px;
    font-weight: 500;
}
@media (max-width: 1280px){
    .loginsignup{
        padding-top: 50px;
    }
    .loginsignup-container{
        width: auto;
        max-width: 500px;
        max-height: 500px;
    }
    .loginsignup-container h1{
        margin: 10px 0px;
    }
    .loginsignup-fields{
        gap: 20px;
        margin-top: 20px;
    }
    .loginsignup-fields input{
        height: 65px;
    width: 90%;   
    }
    .loginsignup-container button {
        width: 100%;
    }
    .loginsignup-login{
        font-size: 16px;
    }
    .login-signup-agree{
        font-size: 14px;
        gap: 10px;
    }

}
@media (max-width: 800px) {
    .loginsignup-container{
        padding: 20px 30px;
    }
    .loginsignup-container h1{
        font-size: 24px;
    }
    .loginsignup-fields{
        margin-top: 20px;
    }
    .loginsignup-fields input{
        height: 50px;
    }    
    .loginsignup-container button {
        height: 50px;
        font-size: 16px;
    }
}
