.offers{
    width: 65%;
    height :60vh;
    display: flex;
    margin :auto;
    margin-top: 150px;
    padding: 0px 140px;
    margin-bottom: 150px;
    background: linear-gradient(180deg,#fde1ff,#e1ffea22 60%);
}

.offers-left{
    flex:1;
    display:flex;
    flex-direction: column;
    justify-content: center;
}
.offers-left h1{
    color: #171711;
    font-size: 80px;
    font-weight: 600;
}
.offers-left p{

    color :#171717;
    font-size: 22px;
    font-weight: 600;
}
.offers-left button{
    width: 282px;
    height: 70px;
    border-radius: 35px;
    background-color: #ff4141;
    border :none;
    font-size: 22px;
    font-weight: 600;
    color: white;
    margin-top: 30px;
    cursor: pointer;
}
.offers-right{
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: top 50px; ;
}
@media(max-width: 1280px){
    .offers{
        padding: 0px 80px;
        margin-bottom: 120px;
    }
    .offers-left h1{
        font-size: 40px;
    }
    .offers-left p{
        font-size: 18px;
    }
    .offers-right img{
        width: 300px;
    }
    .offers-left button{
        width: 250px;
        height: 50px;
        font-size: 15px;
    }
    .offers-right{
        padding-top: 30px; ;
    }
}
@media(max-width: 1024px){
    .offers{
        padding: 0px 60px;
        margin-bottom: 80px;
        height: 40vh;
    }
    .offers-left h1{
        font-size: 30px;
    }
    .offers-left p{
        font-size: 16px;
    }
    .offers-right img{
        width: 200px;
    }
    .offers-left button{
        width: 160px;
        height: 40px;
        font-size: 16px;
    }
    .offers-right{
        padding-top: 10px; ;
    }
}
@media(max-width: 880px){
    .offers{
        
        margin-bottom: 60px;
    }
    .offers-left h1{
        font-size: 25px;
    }
    .offers-left p{
        font-size: 13px;
    }
    .offers-right img{
        width: 180px;
    }
    .offers-left button{
        width: 140px;
        height: 30px;
        font-size: 13px;
    }
}
@media(max-width: 500px){
    .offers{
        height: 25vh;
        margin-bottom: 50px;
    }
    .offers-left h1{
        font-size: 16px;
    }
    .offers-left p{
        font-size: 10px;
    }
    .offers-right img{
        width: 120px;
    }
    .offers-left button{
        width: 120px;
        height: 25px;
        font-size: 10px;
        margin-top: 12px;
    }
}

